@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: rgba(240, 240, 240, .3);
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  color: white;
}

.catalog {
  display: flex;
  flex-direction: column;
  width: 95%;
  max-width: 1200px;
  margin: 20px auto;
}

.catalog__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.header__title {
  font-size: 34px;
  margin: 0 20px 40px 0;
  color: orange;
}

.header__title--brown {
  color: brown;
}

.header__input {
  font-family: "Lato", sans-serif;
  width: 200px;
  border: none;
  padding-bottom: 5px;
  font-size: 14px;
  outline: none;
  background-color: transparent;
  color: white;
}

.header__input::-webkit-input-placeholder {
  color: white;
}

.header__input:-ms-input-placeholder {
  color: white;
}

.header__input::-ms-input-placeholder {
  color: white;
}

.header__input::placeholder {
  color: white;
}

.header__input-subtitle {
  padding-top: 5px;
  font-size: 14px;
  /* color: #555; */
  height: 35px;
}

.header__line {
  height: 2px;
  background-color: orange;
}

.header__line--color {
  background-color: brown;
}

.catalog__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 20px 10px;
  font-size: 12px;
}



.catalog__loader {
  align-self: center;
}

.error {
  font-size: 34px;
  text-align: center;
}

.catalog__buttons-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}



@media (min-width: 550px) {
  .catalog__header {
    justify-content: space-between;
  }
}

.loader {
  width: 100%;
  height: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.catalog__button {
  margin-top: 20px;
  align-self: center;
  outline: none;
  padding: 10px 30px;
  font-size: 28px;
  background-color: orange;
  color: white;
  border: none;
  transition: 0.3s;
  cursor: pointer;
  border-radius: 3px;
}

.catalog__button:hover {
  background-color: brown;
}

.catalog__button--smaller {
  font-size: 12px;
  padding: 5px;
  margin: 5px;
  width: 65px;
  font-weight: bold;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 0px 10px;
  margin: 20px 0px;
  box-shadow: 0px 0px 4px 2px #ccc;
  transition: .3s;
  background-color: rgba(255, 255, 255, 0.2);
}

.card:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.card__img {
  margin-bottom: 10px;
}

.card__type-name {
  margin: 5px;
  display: flex;
  justify-content: space-between;
}

.card__type-name:last-child {
  padding-bottom: 10px;
}

.card__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px;
}

.card__type {
  font-weight: bold;
}



.card__name {
  font-size: 22px;
  margin-right: 5px;
  font-weight: bold;
}

